import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';

import logo from '../../logo-dark.svg';

import "../style.css";

function Header() {
  return (
    <>
      <Navbar bg="dark" variant="dark" id="navbar" className='navbar navCustom' expand="lg" collapseOnSelect sticky="top">
        <Container>
          <Navbar.Brand href="/">
            <img
              alt="logo"
              src={logo}
              width="35"
              height="35"
              className="d-inline-block align-top"
            />{' '}
            _Art_ArtWorld_
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav"/>
          <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto ">
            <Nav.Link href="https://www.instagram.com/_art_artworld_/?igshid=YmMyMTA2M2Y%3D" target="_blank">View on Instagram</Nav.Link>
            <Nav.Link href="/#/">Pricing</Nav.Link>
            <NavDropdown title="Contact Us" id="collasible-nav-dropdown">
              <NavDropdown.Item href="mailto:info@artartworld.ml">info@artartworld.ml</NavDropdown.Item>
              <NavDropdown.Item href="mailto:feedback@artartworld.ml">feedback@artartworld.ml</NavDropdown.Item>
              <NavDropdown.Item href="mailto:developer@artartworld.ml">developer@artartworld.ml</NavDropdown.Item>
              {/* <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">
                Separated link
              </NavDropdown.Item> */}
            </NavDropdown>
          </Nav>
          <Nav>
            <Nav.Link eventKey={1} href="/#/">Home</Nav.Link>
            <Nav.Link eventKey={2} href="/#/about">
              About
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>

        </Container>
      </Navbar>
    </>
  );
}

export default Header;