import "../style.css";

import { Image } from "react-bootstrap";
import { useState, useRef, useEffect } from "react";
import { LoaderChange, LoaderPump } from "../Loader/Loader";

function Home() {
  const [modalShow, setModalShow] = useState({ id: 1, url: img1, view: false });

  const local_images = [
    {
      id: 1,
      url: img1,
      vdoUrl: null,
    },
    {
      id: 2,
      url: img2,
      vdoUrl: null,
    },
    {
      id: 3,
      url: img3,
      vdoUrl: null,
    },
    {
      id: 4,
      url: img4,
      vdoUrl: null,
    },
    {
      id: 5,
      url: img5,
      vdoUrl: null,
    },
    {
      id: 6,
      url: img6,
      vdoUrl: null,
    },
    {
      id: 7,
      url: img7,
      vdoUrl: null,
    },
    {
      id: 8,
      url: img8,
      vdoUrl: null,
    },
    {
      id: 9,
      url: img9,
      vdoUrl: null,
    },
    {
      id: 10,
      url: img10,
      vdoUrl: vdo10,
    },
    {
      id: 11,
      url: img11,
      vdoUrl: vdo11,
    },
    {
      id: 12,
      url: img12,
      vdoUrl: vdo12,
    },
    {
      id: 13,
      url: img13,
      vdoUrl: vdo13,
    },
    {
      id: 14,
      url: img14,
      vdoUrl: vdo14,
    },
    {
      id: 15,
      url: img15,
      vdoUrl: vdo15,
    },
    {
      id: 16,
      url: img16,
      vdoUrl: vdo16,
    },
    {
      id: 17,
      url: img17,
      vdoUrl: vdo17,
    },
    {
      id: 18,
      url: img18,
      vdoUrl: vdo18,
    },
    {
      id: 19,
      url: img19,
      vdoUrl: vdo19,
    },
    {
      id: 20,
      url: img20,
      vdoUrl: vdo20,
    },
    {
      id: 21,
      url: img21,
      vdoUrl: vdo21,
    },
  ];

  // const images = [
  //   {
  //     id: "1",
  //     url: "https://drive.google.com/uc?id=1BPhoTrG8IfHRmu3qq6vGPmUQnp4-EZJv",
  //   },
  //   {
  //     id: "2",
  //     url: "https://drive.google.com/uc?id=14sG2nZUiv-XFJPZbjDAoh1wDnmLy29qo",
  //   },
  //   {
  //     id: "3",
  //     url: "https://drive.google.com/uc?id=1k-BJAT8QNf6WjLbMdOcT_kTDFqH4EfU3",
  //   },
  //   {
  //     id: "4",
  //     url: "https://drive.google.com/uc?id=1VAMpjrc1u64JDtEiK7s130KfgS3ZZwGe",
  //   },
  //   {
  //     id: "5",
  //     url: "https://drive.google.com/uc?id=1GPo33FDYKoZn4AlJ7pkzt_b8hGUBISdI",
  //   },
  //   {
  //     id: "6",
  //     url: "https://drive.google.com/uc?id=1TOpoIAZaaefWJ7Z6XAx9UJuZNdotNa1f",
  //   },
  //   {
  //     id: "7",
  //     url: "https://drive.google.com/uc?id=1NF2uuelMAYRLcFU7L7ZOBeBTXlhLZIKS",
  //   },
  //   {
  //     id: "8",
  //     url: "https://drive.google.com/uc?id=1Uj9jGOFcTyprzyzPll7Hif_a-nTqsscV",
  //   },
  //   {
  //     id: "9",
  //     url: "https://drive.google.com/uc?id=1vs1254koMuj0DTr-pgIUq0iaJRw1WjBK",
  //   },
  // ];

  const setPrevNextImg = (id) => {
    setModelLoading(true);
    if (id !== 0) {
      const prevNextImg = local_images.filter((item) => {
        return item.id === id;
      });
      if (prevNextImg.length !== 0) {
        setModalShow({
          id: prevNextImg[0].id,
          url: prevNextImg[0].url,
          vdoUrl: prevNextImg[0].vdoUrl,
          view: true,
        });
      } else {
        setModalShow({ id: 1, url: img1, vdoUrl: null, view: true });
      }
    } else {
      setModalShow({
        id: local_images[local_images.length - 1].id,
        url: local_images[local_images.length - 1].url,
        vdoUrl: local_images[local_images.length - 1].vdoUrl,
        view: true,
      });
    }
  };

  const [loading, setLoading] = useState(true);
  const counter = useRef(0);
  const imageLoaded = () => {
    counter.current += 1;
    if (counter.current >= local_images.length) {
      setLoading(false);
    }
  };

  const [modelLoading, setModelLoading] = useState(true);
  useEffect(() =>{
    if(modalShow.view){
      setModelLoading(true);
    }
  },[modalShow.view])
  // const modelCounter = useRef(0);
  const modelImageLoaded = () => {
    // console.log("first")
    // modelCounter.current += 1;
    // console.log("to false after load",modelLoading);
    setModelLoading(false);
    // if (modelCounter.current === 0) {
    //   setModelLoading(false);
    //   modelCounter.current = 1;
    // }
    // if (modelCounter.current === 1) {
    //   setModelLoading(true);
    //   modelCounter.current = 0;
    // }
  };

  // const modelLoadStart = () => {
  //   // modelCounter.current += 1;
  //   console.log("to loadinggggggg");
  //     // setModelLoading(true);

  // };

  function MyVerticallyCenteredModal(props) {
    return (
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">Art</Modal.Title>
        </Modal.Header>
        <Modal.Body>
        {modelLoading && <div  className="modelLoader"><LoaderChange /></div>} 
          <div className={modelLoading?"modalDiv hideDiv":"modalDiv"}>
            {modalShow.vdoUrl ? (
              <video className="modelImgVdo" autoPlay={true} loop={true} onPlay={modelImageLoaded}>
                <source src={modalShow.vdoUrl} type="video/mp4" />
              </video>
            ) : (
              <Image
                className="modelImgVdo"
                src={modalShow.url}
                rounded
                fluid
                onLoad={modelImageLoaded}
              />
            )}
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Pagination>
            <Pagination.Prev onClick={() => setPrevNextImg(modalShow.id - 1)} />
            <Pagination.Item>{modalShow.id}</Pagination.Item>
            <Pagination.Next onClick={() => setPrevNextImg(modalShow.id + 1)} />
          </Pagination>
        </Modal.Footer>
      </Modal>
    );
  }

  return (
    <>
      {loading && <LoaderPump />}
      <Container className={loading && "hideDiv"}>
        <br></br>
        <br></br>
        <br></br>

        {/* <Row>
          <h1>Hello</h1>
        </Row> */}
        <Row>
          <MyVerticallyCenteredModal
            show={modalShow.view}
            onHide={() =>
              setModalShow({ id: 1, url: img1, vdoUrl: null, view: false })
            }
          />
          {local_images.map(function (img, i) {
            return (
              <Col xs={6} md={4} className="cusCol" key={i}>
                <div className="cusIng">
                  {" "}
                  <Button
                    variant=""
                    onClick={() =>
                      setModalShow({
                        id: img.id,
                        url: img.url,
                        vdoUrl: img.vdoUrl,
                        view: true,
                      })
                    }
                  >
                    <Image onLoad={imageLoaded} src={img.url} rounded fluid />
                  </Button>
                </div>
              </Col>
            );
          })}
        </Row>
      </Container>
    </>
  );
}

export default Home;
